.buy-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
}

.buy-product-img {
    width: 300px;
    height: 300px;
    object-fit: cover;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.buy-container div {
    max-width: 500px;
    margin-left: 20px;
}

h1 {
    font-size: 24px;
    margin-bottom: 10px;
    color: #333;
}

p {
    font-size: 16px;
    color: #666;
    margin-bottom: 20px;
}

.qt-container {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.quantity-btn {
    cursor: pointer;
    background-color: #3498db;
    color: #fff;
    padding: 10px;
    border-radius: 4px;
    margin: 0 5px;
    transition: background-color 0.3s;
}

.quantity-btn:hover {
    background-color: #2980b9;
}

.quantity-text {
    font-size: 18px;
    font-weight: bold;
    margin: 0 10px;
}

.shippingAddress {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.Buy-btn {
    cursor: pointer;
    background-color: #27ae60;
    color: #fff;
    padding: 15px;
    border: none;
    border-radius: 4px;
    font-size: 18px;
    transition: background-color 0.3s;
}

.Buy-btn:hover {
    background-color: #219653;
}
